import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { CircularProgress, Snackbar, Divider } from '@material-ui/core';
import axios from 'axios';
import formData from './config/workshop.json';
import styled from 'styled-components';


const ImgTag = styled.img`
  height: 100%;
  max-width: 30vw;
  margin-left: auto;
  @media(orientation:portrait) {
    max-width: 100vw;
  }
`;

class WorkshopForm extends Component{
  constructor(props){
    super(props);
    this.state={
      submitLoading: false,
      errorMessage: '',
      error: false,
      submitSuccess: false,
      submitInfo:{},
      formError:{
        name: '',
        email: '',
        mobile: '',
        institute: '',
        city: '',
        state: '',
        zip: ''
      },
      values:{
        name: '',
        email: '',
        mobile: '',
        institute: '',
        city: '',
        state: '',
        zip: ''
      }
    };
  }

  onFormChange(val, field){
    
    this.setState(prevState =>{
      return ({
        ...prevState,
          values:{
            ...prevState.values,
            [field]: val
        },
        formError:{
          ...prevState.formError,
          [field]: ''
        },
      });
    });
  }

  handleSubmission(){
    this.setState({
      submitLoading: true
    });
    axios({
      method: 'POST',
      url: '/payment/pay',
      data: {
        ...this.state.values,
        amount: 150000,
        workshop: 'drone'
      }
    }).then((response)=>{
      console.log(response);
      this.setState({
        submitInfo:{
          ...response.data
        },
        submitLoading: false,
        submitSuccess: true
      }, ()=>{
        const {submitInfo, values}= this.state;
        const script = document.createElement("script");
        script.onload=function(){
          document.getElementsByClassName('razorpay-payment-button')[0].style.display='none';
          document.getElementsByClassName('razorpay-payment-button')[0].click();
        }
        const attributes={
          'data-key': submitInfo.key,
          'data-amount': "150000" ,
          'data-currency':"INR",
          'data-order_id': submitInfo.orderID,
          'data-buttontext': "Continue to Payment",
          'data-name': "Tecnoesis 2020",
          'data-description': "Tecnoesis 2020 NIT Silchar",
          'data-prefill.name' :values.name,
          'data-prefill.email': values.email,
          'data-prefill.contact': values.mobile
        };
        script.src = "https://checkout.razorpay.com/v1/checkout.js";

        for(var key in attributes){
          script.setAttribute(key, attributes[key]);
        }
        script.async = true;
        document.forms['reg'].appendChild(script);
      });
    }).catch((e)=>{
      console.log(e.response);
      if(e.response.data.error==='email'){
        this.setState({
          submitLoading: false,
          error: true,
          formError:{
            email: 'Email Already Registered'
          },
          errorMessage: 'Email Already Registred'
        });
      }
      else{
        this.setState({
          submitLoading: false,
          error: true,
          errorMessage: 'Error Occurred'
        });
      }
    });
  }

  onTextBlur(field){
    if(field==='email'){
      if(!/(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/.test(this.state.values.email)){
        this.setState({
          formError:{
            email: 'Invalid Email'
          }
        })
      }
    }
    else if(field==='mobile'){
      if(this.state.values.mobile.length!==10){
        this.setState({
          formError:{
            mobile: 'Invalid Mobile Number. Must be of 10 digits.'
          }
        })
      }
    }
  }

  render(){
    const { values, submitSuccess, formError } =this.state;
    const isDisabled = values.name==='' || values.email==='' || values.zip==='' || values.institute==='' || values.city==='' || values.state==='' || values.mobile==='' || (formError.email && !formError.email.length) || (formError.mobile && !formError.mobile.length);

    return (
      <Grid container spacing={0}>
          <Grid item sm={12} md={6} xs={12} style={{ backgroundColor: '#007792', color: '#fff', padding: '0', minHeight: '85vh', textAlign: 'center'}} >
            <ImgTag src="/form/workshoplogo.jpg" alt="Workshop Registration"
            />
          </Grid>
          <Grid item sm={12} md={6} xs={12} style={{ padding: '2vh 2vw', color: '#007792' }} >

            <Typography component="h1" variant="h4" align="center">
              Tecnoesis 2020

            </Typography>
      <React.Fragment>
        <Grid item xs={12} sm={12} style={{margin: '5% 0%', textAlign: 'center', fontSize: '2.5vh'}}>
          Registration For Workshop
        </Grid>
        <Grid item xs={12} sm={12}>
          <Typography variant="h6" gutterBottom align="left">
            Contact Information
          </Typography>
        </Grid>
        <Grid container spacing={3}>
          {formData.map((form)=>{
            if(form.type==='information'){
              return(
                <Grid item xs={12} sm={12} key={form.name}>
                  <Typography variant="h6" gutterBottom align="left">
                    {form.text}
                  </Typography>
                </Grid>
              );
            }
            return(
              <Grid item xs={form.grid.xs} sm={form.grid.sm} key={form.name}>
                <TextField
                  required={form.required}
                  name={form.name}
                  label={form.label}
                  error={formError[form.name] && formError[form.name].length>0}
                  helperText={formError[form.name]}
                  value={values[form.name] || ''}
                  fullWidth={form.fullWidth}
                  onChange={(event)=>{
                    this.onFormChange(event.target.value, form.name);
                  }}
                  onBlur={(event)=>{
                    this.onTextBlur(form.name);
                  }}
                />
              </Grid>
            )
          })}
        </Grid>

        <Divider variant="middle" style={{ margin: '2vh 0' }} />

        <Grid item xs={12} sm={6}>
          <TextField
            label="Registration Fees"
            defaultValue="1500"
            disabled
          />
        </Grid>
    
        <div style={{ marginTop: '4vh', textAlign: 'center' }}>
          <Button
            variant="contained"
            disabled={isDisabled || this.state.submitLoading || this.state.submitSuccess}
            style={{ backgroundColor: '#007792', color: '#fff', marginRight: '20px' }}
            onClick={()=>{
              this.handleSubmission();
            }}
          >
            {this.state.submitLoading && (<CircularProgress style={{ color: "#fff" }} />)}
            {!this.state.submitLoading && ("Continue To Payment")}
          </Button>

          <Button variant="contained" color="default" href="/misc/DroneWorkshop.pdf" style={{ margin:' 2vh 0' }}>
            Download Workshop Schedule
          </Button>
        </div>
        {submitSuccess && (
          <form action="/payment/success/" method="POST" name="reg">
            <input type="hidden" custom="Hidden Element" name="hidden" />
          </form>
        )}
        <Snackbar
          open={this.state.error}
          anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
          message={this.state.errorMessage}
          variant="error"
          onClose={()=> {
            this.setState({
              error: false
            })
          }}
          autoHideDuration={5000}
        />
      </React.Fragment>
      </Grid>
    </Grid>
    );
  }
}

export default WorkshopForm;
