import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import styled from 'styled-components';
import axios from 'axios';
import Typography from '@material-ui/core/Typography';


const ImgTag = styled.img`
  height: 100%;
  max-width: 30vw;
  margin-left: auto;
  @media(orientation:portrait) {
    max-width: 100vw;
  }
`;

const HolaText = styled.div`
  text-align: center;
  font-size: 3vh;
  margin: 5vh 0vh 3vh;
`;

const UserValue = styled.div`
  font-size: 2vh;
`;

class WorkshopForm extends Component{
  constructor(props){
    super(props);
    this.state={
      infoLoading: false,
      values:{
        name: 'Aryan Singh',
        email: 'aryan.major@gmail.com',
        mobile: '9954959101',
        whatsapp: '',
        institute: 'NIT',
        city: 'Silchar',
        state: 'Assam',
        zip: '788010'
      }
    };
  }

  componentDidMount(){
    axios({
      method: 'GET',
      url: '/workshop/fetchWorkshop/'+this.props.match.params.id
    }).then((response)=>{
      this.setState({
        values: {
          ...response.data
        }
      });
    }).catch((err)=>{
      console.log(err);
    })
  }

  render(){
    const { values} =this.state;
    return (
      <Grid container spacing={0}>
          <Grid item sm={12} md={6} xs={12} style={{ backgroundColor: '#007792', color: '#fff', padding: '0', minHeight: '85vh', textAlign: 'center'}} >
            <ImgTag src="/form/workshoplogo.jpg" alt="Workshop Registration"
            />
          </Grid>
          <Grid item sm={12} md={6} xs={12} style={{ padding: '2vh 2vw', color: '#007792' }} >

            <Typography component="h1" variant="h4" align="center">
              Tecnoesis 2020

            </Typography>
      <React.Fragment>
        <Grid item xs={12} sm={12} style={{margin: '5% 0%', textAlign: 'center', fontSize: '2.5vh'}}>
          Successful Registration For Workshop
        </Grid>
        <Divider variant="middle" />
        <Grid item xs={12} sm={12}>
          <HolaText>
            Hola <strong>{values.name}</strong>,
          </HolaText>
          <UserValue>
            Thank you for registering for Drone Workshop.<br />
            A payment details mail has been sent on {values.email}. <br />
            <br />
            <strong style={{ marginRight: '2vw' }}>Transaction ID</strong>
            {values.payment_id}
            
          </UserValue>

          <Divider variant="middle" style={{ margin: '4vh 0'}} />
          <Button variant="contained" color="default" href="/misc/DroneWorkshop.pdf">
            Download Workshop Schedule
          </Button>
        </Grid>
        
        
      </React.Fragment>
      </Grid>
      </Grid>
    );
  }
}

export default WorkshopForm;
