import React, { Component } from 'react';
import {Switch, Route} from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import { Grid } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import AddressForm from './InputForm';
import WorkshopForm from './WorkshopForm';
import WorkshopSuccess from './WorkshopSuccess';
import styled from 'styled-components';

const FormPaper = styled(Paper)`
  width: 60vw;
  padding: 0;
  margin: 5vw 20vw 2vw;

  @media(orientation:portrait) {
    width: 100vw;
    margin: 0;
  }
`;

const ImgTag = styled.img`
  height: 100%;
  max-width: 30vw;
  margin-left: auto;
  @media(orientation:portrait) {
    max-width: 100vw;
  }
`;


function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://tecnoesis.org/">
        Tecnoesis 2020
      </Link>
      {'.'}
    </Typography>
  );
}

class Form extends Component{
  
  
  render(){
    return (
      <React.Fragment>
        <CssBaseline />
        <main >
          <FormPaper>
            <React.Fragment>
            <Switch>
              <Route path='/form/ca' component={AddressForm} />
              <Route path='/form/workshop/success/:id' component={WorkshopSuccess} />
              <Route path='/form/workshop' component={WorkshopForm} />
            </Switch>
            </React.Fragment>
          </FormPaper>
          <Copyright />
        </main>
      </React.Fragment>
    );
  }
}

export default Form;